import React from 'react';
import { TabBar } from 'antd-mobile';
import { connect } from 'react-redux';
import { Icon } from 'antd';

interface DashboardMobileState {
  selectedTab: 'servicesTab' | 'personalAreaTab' | 'logoutTab';
  hidden: boolean;
}

class DashboardMobile extends React.Component<any, DashboardMobileState> {
  constructor(props: any) {
    super(props);
    this.state = {
      selectedTab: 'servicesTab',
      hidden: false
    };
  }

  private _onClickVisibleChange = (e: any) => {
    e.preventDefault();
    this.setState({
      hidden: !this.state.hidden
    });
  };

  renderContent(pageText: any) {
    return (
      <div style={{ backgroundColor: 'white', height: '100%', textAlign: 'center' }}>
        <div style={{ paddingTop: 10 }}>
          Clicked “{pageText}” tab， show “{pageText}” information
        </div>
        <a href="!#" style={{ display: 'block', marginTop: 40, marginBottom: 20, color: '#108ee9' }} onClick={this._onClickVisibleChange}>
          Click to show/hide tab-bar
        </a>
      </div>
    );
  }

  render() {
    return (
      <div style={{ position: 'fixed', height: '100%', width: '100%', top: 0 }}>
        <TabBar
          unselectedTintColor="#949494"
          tintColor="#33A3F4"
          barTintColor="white"
          tabBarPosition="bottom"
          hidden={this.state.hidden}
          prerenderingSiblingsNumber={0}
        >
          <TabBar.Item
            title="Servizi"
            key="Servizi"
            icon={<Icon type="calendar" />}
            selectedIcon={<Icon type="calendar" />}
            selected={this.state.selectedTab === 'servicesTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'servicesTab'
              });
            }}
            data-seed="logId"
          >
            <div>TODO: Services</div>
          </TabBar.Item>
          <TabBar.Item
            title="Area personale"
            key="Area personale"
            icon={<Icon type="user" />}
            selectedIcon={<Icon type="user" />}
            selected={this.state.selectedTab === 'personalAreaTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'personalAreaTab'
              });
            }}
            data-seed="logId1"
          >
            {this.renderContent('personalArea')}
          </TabBar.Item>
          <TabBar.Item
            title="Logout"
            key="Logout"
            icon={<Icon type="logout" />}
            selectedIcon={<Icon type="logout" />}
            selected={this.state.selectedTab === 'logoutTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'logoutTab'
              });
            }}
          >
            {this.renderContent('Logout')}
          </TabBar.Item>
        </TabBar>
      </div>
    );
  }
}

const mapStateToProps = (state: any, ownProps: any) => {
  return {
    user: state.appData.user,
    committee: state.appData.committee
  };
};

const DashboardMobileContainer = connect(mapStateToProps)(DashboardMobile);
export default DashboardMobileContainer;
